  --pastel-orange: #DE8F75;
  --light-blue: #5F75AA;
  --gray: #AEB6C1;
  --pastel-yellow: #E0DA9C;
  --bluish-gray: #A5C3C0;
  --marine-blue: #2A5A75;
  --black: #202020;
  --forest-green: #32917B;
  --red: #E35442;
  --off-white: #F2F2F2;
  --grayish-white: #EBEBEB;
  --lighter-gray: #E3E3E3;
  --light-gray: #CCCCCC;
  --medium-gray: #ADADAD;
  --dark-gray: #7F7F7F;
  --darker-gray: #686868;

  /*fonts*/
  --open-sans: 'Open Sans', sans-serif;
  --gotham: GothamBook, sans-serif;
  --gotham-bold: GothamBold, sans-serif;
}
.App {
  background-color: #fff;
  color: var(--black);
  font-family: var(--open-sans)
}
.App--modal{
  height: 100vh;
  overflow: hidden;
}
.logo-container{
  padding: 26px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center
}

.main-menu{
  height: 50px;
