  border: 5px solid white;
  cursor: pointer;
}
.color-circle--small{
  height: 13px;
  width: 13px;
  margin-right: 5px;
  cursor: default;
}
.color-circle--active{
  border-color: var(--pastel-yellow);
}
.size-circle{
  padding: 5px;
  margin-right: 3px;
  border-radius: 50%;
  background: var(--lighter-gray);
