}
.button{
  background: var(--forest-green);
  color: #fff;
  font-size: 18px;
  border-radius: 3px;
  border: none;
  display: block;
  width: 100%;
  height: 60px;
  margin-top: 53px;
  margin-bottom: 16px;
  cursor: pointer;
}
.suggested-products{
  margin-bottom: 89px;
}
.suggested-products ul{
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
