  display: flex;
  padding-left: 0;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.links__group{
  line-height: 42px;
  list-style-type: none;
}
.links__group--title{
  font-family: var(--gotham-bold);
  color: var(--light-gray);
  font-size: 14px;
}
.newsletter{
  border-radius: 50%;
