  width: 293px;
  font-size: 14px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  position: relative;
  left: 70vw;
  bottom: 40px;
  margin-top: -280px;
  background: white;
}
.newsletter__title{
  font-size: 16px;
