  color: white;
  padding-left: 130px;
  padding-right: 92px;
  display: -ms-flexbox;
  display: flex;
  font-size: 12px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.address span{
  display: block;
}

/*modal*/
.modal{
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(32, 32, 32, 0.6)
}
.modal-content{
  width: 445px;
  height: 602px;
  background: white;
  padding: 36px 77px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 62px auto 0;
}
.modal__close{
  margin-left: 300px;
  cursor: pointer;
}
.modal__image{
  max-width: 100%;
}
.modal__title{
  color: var(--pastel-orange);
  font-family: var(--gotham-bold);
  font-size: 22px;
  margin: 0;
}
.modal__button{
