  display: block;
  text-align: center;
  font-family: var(--gotham);
}
/*utilities*/
.link{
  color: var(--pastel-orange);
  text-decoration: underline
}
.link--position{
  margin-left: 100px;
}

.display-none{
  display: none;
}
.margin-bottom{
  margin-bottom: 5px
}
.margin-top{
  margin-top: 14px;
}
/*background*/
.red-bg{
  background: var(--red) 
}
.black-bg{
