}
.gray-bg{
  background: var(--gray) 
}
.forest-green-bg{
  background: var(--forest-green) 
}

/*colors*/
.pastel-orange{
  color: var(--pastel-orange)
}
.red{
  color: var(--red);
}
.light-blue{
  color: var(--light-blue);
}

